import { db } from "@/firebase";
import store from '@/store'
export default {
    apiGetUsersOffice(officeId) {
        let users = []
        db.collection('usersApp')
            .where('locationId', '==', officeId)
            .where('admin', '==', false)
            .where('active', '==', true)
            .onSnapshot(snapshot => {
                users = []
                if (!snapshot.empty) {
                    snapshot.forEach(user => {
                        users.push(Object.assign({ id: user.id }, user.data()))
                    });
                }
                store.commit("setUsersOffice", users)
            })
    },
    async apiMonitorQueue(userId) {
        try {
            const resp = await db.collection('clientsRequests')
            .where('block', '==', userId)
            .where('status', '==', 'en proceso...')
            .get()
            
            return resp
          } catch (err) {
               console.log(err)
            }
    },

}