<template>
  <div class="Chart">
    <v-subheader>Monitor Gestión de Cola</v-subheader>
    <v-data-table :headers="headersUsers" :items="usersOffice" hide-default-footer>
      <template v-slot:item.status="{ item }">
        <v-chip color="success" dark v-if="item.status">Disponible</v-chip>
        <v-chip color="warning" dark v-else>Ocupado</v-chip>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      usersOffice: [],
      headersUsers: [
        { text: "Nombre", value: "name", align: "left" },
        { text: "Estatus", value: "status", align: "center" },
        { text: "Req. procesados", value: "count", align: "center" },
        { text: "TMA", value: "tma", align: "center" },
        { text: "TTA", value: "tma", align: "center" }
      ]
    };
  },
  computed: {
    ...mapGetters(["getUsersOffice",])
  },
  methods: {
    fillData() {
      this.usersOffice = [];
      let status = false;
      this.getUsersOffice.forEach(async user => {
        let request = await this.$store.dispatch("getMonitorQueue", user.id);
        request.forEach(r => {
          status = r.empty;
        });
        this.usersOffice.push({
          name: user.name,
          id: user.id,
          status
        });
      });
    }
  },
  mounted() {
    this.fillData();
  }
};
</script>